@tailwind base;
@tailwind components;
@tailwind utilities;


@layer base{
    body {
        @apply bg-slate-900 font-body px-5 text-white;
    }
}

@layer components {
    .page-container {
        max-width: 1280px;
        @apply mx-auto;
    }
    .movie-list .swiper-slide {
        width: 300px;
        height: auto;
    }
    .banner .swiper {
        height: 100%;
    }
    
}
//pagination    
.pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    .selected {
        @apply text-primary;
    }
}
@media  screen and (max-width:480px) {
    .pagination {
        gap: 8px;
        font-size: 10px;
    }
}
.skeleton {
    background-color: #eee;
    background-image: linear-gradient(
      110deg,
      #ececec 8%,
      #f5f5f5 18%,
      #ececec 33%
    );
    background-size: 200% 100%;
    animation: 1.5s shine linear infinite;
  }
  @keyframes shine {
    to {
      background-position-x: -200%;
    }
  }